
import { Options, Vue } from "vue-class-component";
import EditableSelect from "./EditableSelect.vue";

@Options({
  props: { value: Number },
  components: {
    EditableSelect,
  },
})
export default class InputDial extends Vue {
  mouseDown = false;
  externalChange = false;
  angle = 0.0;
  style = "";
  startX = 0;
  startY = 0;
  weight = 20;
  val = 0;
  revs = 1;
  currentUnit = "px";
  floatUnits = ["em", "rem"];
  startTime = 0;

  get value(): number {
    return (this.$props as any).value;
  }

  mounted() {
    this.val = this.value;

    //update on external mods
    this.$watch("value", (val: number, _old: number) => {
      this.externalChange = true;
      this.val = val;
    });

    //map bidirectional val to degree
    this.$watch("val", (val: number, old: number) => {
      if (!this.mouseDown) {
        this.angle = (360 * val) / this.weight;
        this.style = `transform: rotate(${this.angle}deg);`;
      }
      if (!this.externalChange) {
        this.$emit("update:value", val);
        this.$emit("valueChanged", {
          value: val,
          unit: this.currentUnit,
        });
      }
    });

    document.addEventListener("mousemove", (e) => {
      if (this.mouseDown) {
        const rect = (this.$refs.base as HTMLElement).getBoundingClientRect();
        const delta =
          (this.getAngle(
            rect.x + rect.width / 2,
            rect.y + rect.height / 2,
            this.startX,
            this.startY,
            e.clientX,
            e.clientY
          ) *
            180) /
          Math.PI;
        this.angle += delta;
        this.revs = Math.floor(Math.abs(this.angle / 360) + 1);
        this.startX = e.clientX;
        this.startY = e.clientY;
        this.style = `transform: rotate(${this.angle}deg);`;
        let weight = this.floatUnits.includes(this.currentUnit)
          ? 1
          : this.weight;
        this.externalChange = false;
        this.val += (delta / 360) * weight * this.revs;
        this.val =  Number.parseFloat(this.val.toFixed(3));
      }
    });
    document.addEventListener("mouseup", (e) => {
      if (this.mouseDown) {
        this.mouseDown = false;
        this.angle = this.angle % 360;
        this.revs = 1;
        this.weight = 20;
        //console.log();
      }
    });
  }
  handleInputChanged(data: any) {
    this.externalChange = false;
    let num = Number.parseFloat(data.target.value.replace(/[^\d-.]/g, ""));
    num = Number.parseFloat(num.toFixed(1));
    this.val = num;
  }
  handleDoubleClick() {
    this.externalChange = false
    this.val = 0;
  }
  handleUnitChange(data: { unit: string }) {
    this.currentUnit = data.unit;
  }
  canEmit(interval: number): boolean {
    const now = Date.now();
    if (this.startTime == 0) this.startTime = now;
    const delta = now - this.startTime;
    if (delta > interval) {
      this.startTime = now;
      return true;
    }
    return false;
  }
  handleMouseDown(e: MouseEvent) {
    if (!this.mouseDown) {
      this.mouseDown = true;
      this.startX = e.clientX;
      this.startY = e.clientY;
    }
  }
  toggle(value: boolean): boolean {
    if (value) return false;
    else return true;
  }
  getAngle(
    centerX: number,
    centerY: number,
    oldX: number,
    oldY: number,
    newX: number,
    newY: number
  ) {
    var x1 = oldX - centerX;
    var y1 = oldY - centerY;
    var x2 = newX - centerX;
    var y2 = newY - centerY;
    var d1 = Math.sqrt(x1 * x1 + y1 * y1);
    var d2 = Math.sqrt(x2 * x2 + y2 * y2);

    return Math.asin((x1 / d1) * (y2 / d2) - (y1 / d1) * (x2 / d2));
  }
}
