
import { Options, Vue } from "vue-class-component";
import ToolButton from "./ToolButton.vue";
import Scrollbar from "smooth-scrollbar";


@Options({
  props: {
    heading: String,
    closed: Boolean,
    width: Number,
    height: Number,
    animation: String,
    glassed: Boolean,
    fill: String,
  },
  components: { ToolButton },
})
export default class Panel extends Vue {
  closeIcon = "";
  closedState = false;

  get closed(): boolean {
    const value = (this.$props as any).closed;
    return value;
  }

  mounted() {
    Scrollbar.initAll()
    this.closedState = (this.$props as any).closed;
    this.$watch("closed", (value: any, old: any) => {
      this.closedState = value as boolean;
    });
  }

  handleClick() {
    this.$emit("update:closed", true)
    this.$emit("panelClosed", {});
  }
  beforeMount() {
    this.closeIcon = require("../assets/icons/close.svg");
  }
}
